$('.dropdown__phone, .callback').prepend('+996 ');
$('.dropdown__phone, .callback').each(function (index) {
  var phone = $(this).text().replace(/[^0-9]/gi, '');
  var number = parseInt(phone, 10);
  $(this).attr("href", "tel:+" + number);
});

$('.dropdown__mail').each(function (index) {
  var mail = $(this)
    .clone()
    .children()
    .remove()
    .end()
    .text();
  $(this).attr("href", "mailto:" + mail);
});


// phone
$(".phone-js").inputmask({
  "mask": "+999 (999) 999-999"
});
// phone end




// input file
(function () {

  $('.input__uploader').each(function () {
    var $input = $(this),
      $label = $input.next('.js-labelFile'),
      labelVal = $label.html();

    $input.on('change', function (element) {
      var fileName = '';
      if (element.target.value) fileName = element.target.value.split('\\').pop();
      fileName ? $label.addClass('has-file').find('.js-fileName').html(fileName) : $label.removeClass('has-file').html(labelVal);
    });
  });

})();
// input file end


// filter select dropdown
$('.select').on('click', function () {
  $(this).children('.select__dropdown').slideToggle();
});
$('.select__option').on('click', function () {
  let value = $(this).attr('data-value');
  $(this).parent().parent().find('.select-item').val(value);
  $(this).parent().parent().find('.select_checked').text(value);
});
$(document).mouseup(function (e) {
  var div = $(".select");
  if (!div.is(e.target) &&
    div.has(e.target).length === 0) {
    $('.select__dropdown').slideUp();
  }
});
// filter select dropdown end



// accordion
$('.accordion__trigger').on('click', function () {
  $(this).parent().parent().find('.accordion__desc').slideToggle();
  $(this).parent().parent().toggleClass('active');
});
// accordion end





























function initMap() {
  var map = new google.maps.Map(document.getElementById('googlemap'), {
    zoom: 7,
    center: {
      lat: 42.8746212,
      lng: 74.5697617
    }
  });
  var labels = 'ABCDEFGHIJKLMNOPQRSTUVWXYZ';
  var image = new google.maps.MarkerImage(
    "data:image/png;base64,",
    null, null, null, new google.maps.Size(40, 52)); // Create a variable for our marker image.
  var infowindow = new google.maps.InfoWindow({ // Create a new InfoWindow
    content: "This is <strong>Megamall Penang</strong>, <em>one</em> of shopping centres that has a cinema!" // HTML contents of the InfoWindow
  });
  var infoWin = new google.maps.InfoWindow();
  var markers = locations.map(function (location, i) {
    var marker = new google.maps.Marker({
      position: location,
      icon: "images/advantages/banner_" + location.type + ".png"
    });
    google.maps.event.addListener(marker, 'click', function (evt) {
      var contentString = '<div id="content">' +
        '<h1 class="billboardCaption">' + location.info + '</h1>' +
        '<div class="billboardImg"><img width="100%" src="' + location.thumb + '" alt="loc"></div>' +
        '<div class="billboardInfo"><ul><li><i class="far fa-map-marker-alt"></i>' + location.billboardInfo1 +
        '</li><li><i class="fal fa-arrows"></i>' + location.billboardInfo2 + '</li></ul></div>' +
        '<div class="billboardDesc"><p>' + location.billboardDesc +
        '</p><a target="_blank" class="billboardLink" href="' + location.billboardLink +
        '"><i class="flabio-360"></i></a></div>'
      '</div>';
      infoWin.setContent(contentString);
      infoWin.open(map, marker);
    })
    return marker;
  });
  var markerCluster = new MarkerClusterer(map, markers, {
    imagePath: 'https://developers.google.com/maps/documentation/javascript/examples/markerclusterer/m'
  });
}
